.register {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.header-register h1 {
  font-size: 3rem;
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

form input,
button {
  margin-bottom: 20px;
  padding: 10px 8px;
  font-family: "Monospace", "Helvetica";
  border: 1px solid #e3e3e3;
  outline: none;
}

form button {
  background-color: #1bb76e;
  border: none;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.register a {
  color: #1bb76e;
  font-size: 1.5rem;
}

@media screen and (min-width: 425px) {
  .register {
    width: 60%;
    margin: 0 auto;
  }
}
