.container-landing-header h1 {
  text-align: center;
  font-size: 4rem;
}

.container-landing-info {
  margin-bottom: 25px;
}

.container-landing-info p {
  text-align: center;
  font-size: 2rem;
  font-weight: medium;
}

.container-landing-links {
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.container-landing-links a {
  font-size: 1.3rem;
  background-color: #1bb76e;
  padding: 10px 2px;
  width: 40%;
  font-weight: bold;
  color: white;
  text-decoration: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.container-landing-links a:hover {
  opacity: 0.8;
}

@media screen and (min-width: 768px) {
  .container-landing-links {
    justify-content: center;
  }
  .container-landing-links a {
    width: 20%;
  }
  .container-landing-links a:first-child {
    margin-right: 20px;
  }
}
