li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #e3e3e3;
  padding: 2px 8px;
  width: 100%;
  margin-bottom: 20px;
}

li p {
  font-size: 1.2rem;
  cursor: pointer;
}

li img {
  cursor: pointer;
}

.completed {
  text-decoration: line-through;
}
